:root {
  --accent-color: rgb(241, 0, 105);
  --accent-color2: rgb(0, 109, 233);

  --gradient-color1: rgba(238, 174, 202, 1);
  --gradient-color2: rgba(148, 187, 233, 1);
}

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  background: rgb(57, 57, 57);
  background-position-x: 0;
  background-position-y: 0;
  background-size: contain;
  padding-top: 64px;
}

.pixabay-link {
  position: fixed;
  top: 4px;
  left: 4px;
  z-index: 10;
}
.pixabay-logo-image {
  height: 24px;
}

.search-container {
  z-index: 9;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 32px;
  background: linear-gradient(45deg, rgba(238, 174, 202, 1), rgba(148, 187, 233, 1));
}

.search-form {
  /* width: fit-content;
    margin-left: auto;
    margin-right: auto; */

  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.gallery {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.photo-card {
  width: 290px;
  height: 320px;
  /* margin-right: 30px; */
  /* border: 2px solid black; */
  border-radius: 4px;
  margin-bottom: 10px;
  overflow: hidden;
  border: solid 0.5px grey;
  background: linear-gradient(45deg, rgba(238, 174, 202, 1), rgba(148, 187, 233, 1));
}

.photo-card:hover {
  box-shadow: 0px 1px 1px rgba(148, 187, 233, 0.8), 0px 4px 4px rgba(238, 174, 202, 0.56),
    1px 4px 6px rgba(148, 187, 233, 0.8);
}

.photo-card__img {
  /* width: 370px;
  height: 294px; */
  display: block;
  width: 100%;
  height: 85%;
  overflow: hidden;
  z-index: -1;
}

.info {
  display: flex;
  flex-direction: row;
}

.info-item {
  text-align: center;
}

.photo-card__img > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition-duration: 250ms;
}

.photo-card__img > img:hover {
  transform: scale(1.1);
  z-index: -10;
}

.info-accent {
  color: var(--accent-color);
}

.info-item {
  color: var(--accent-color2);
  text-align: center;
}

.load-more {
  margin-top: 24px;
  position: relative;
  bottom: 16px;
  left: 50%;
  transform: translate(-50%, 0);
}

.load-more:disabled {
  display: none;
}

html {
  scroll-behavior: smooth;
}